import * as React from 'react';

import {
  InputNumber
} from 'antd';
import { InputNumberProps } from 'antd/lib/input-number';
import FieldUtil from '../../../../Util/FieldUtil';

// non default props
export interface OpacityFieldProps extends Partial<InputNumberProps> {
  opacity?: number;
}

/**
 * OpacityField
 */
export const OpacityField: React.FC<OpacityFieldProps> = ({
  onChange,
  opacity,
  ...inputProps
}) => {

  return (
    <InputNumber
      className="editor-field opacity-field"
      min={0}
      max={1}
      step={0.01}
      value={opacity}
      onChange={FieldUtil.nullToUndefined(onChange)}
      {...inputProps}
    />
  );
};

export default OpacityField;
